<template lang="">
     <div id="container">
        <h2>
            앨범소개
        </h2>
    <div class="service_wrap">
      <nav>
        <a href="" v-for="(item, index) in songs" :key="index" 
        @click.prevent="detailV(item,index)" :class="{On:onA===index}">
          {{index}}집
        </a>
      </nav>
      <div class="service_content">
        <div>
          <h1>{{album.date}}</h1>
          <p>{{album.title}}</p>
          <img :src="album.img" @click="modal()">
        </div>
      </div>
    </div>
    <modal v-if="modalOpen" :propsdata="songData" :propsimg="modalImg" @closeModal="closeModal"/>
    </div>
</template>
<script>
import songs from '/src/assets/songs.js'
import modal from './Modal/appModal'

export default {
    components:{
    modal
    },
    data() {
    return {
      songs:songs,
      album : songs[0],
      songData : "",
      onA : 0,
      modalOpen : false,
      modalImg : songs[0].img
    }
  },
  methods: {
    detailV(item,index){
      this.onA = index
      this.album = item
    },
    modal(){
        this.modalOpen = true;
        this.songData = this.album.list
        this.modalImg = this.album.img
    },
    closeModal(){
      this.modalOpen = false
    },
  },
}
</script>
<style scoped>
  .service_wrap nav {
    display: flex;
    justify-content: space-around;
    height: 5rem;
    align-items: center;
  }
  .service_wrap nav a {
    display: block;
    height: 100%;
    width: 100%;
    line-height: 5rem;
    border-bottom: 3px solid rgb(224, 224, 224)
  }
  .service_wrap nav a.On {
    background-color: rgb(244, 233, 255);
    color: rgb(97, 47, 143);
    border-bottom: 3px solid rgb(97, 47, 143)
  }
  .service_content>div {
    height: 50rem;
    border: 1px solid rgb(224, 224, 224);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5rem;
  }
  .service_content>div p {
    font-weight: 600;
    font-size: 3rem;
  }
  .service_content>div h4 {
    font-size: 1.6rem;
    font-weight: 200;
  }
  .service_content>div span {
    font-size: 10rem;
    display: inline-block;
    margin: 3rem 0;
    color: rgb(97, 47, 143)
  }
  .svc_btn {
    border: none;
    padding: .5rem 2rem;
    border-radius: 3rem;
    cursor: pointer;
  }
</style>