<template lang="">
  <div id="container">
    <h2>
      게임
    </h2>
    <div class="service_wrap">
      <nav>
        <a href="" v-for="(item, index) in game" :key="index" 
        @click.prevent="detailV(item,index)" :class="{On:onA===index}">
          {{item}}
        </a>
      </nav>
      <div class="service_content">
        <div>
          <span>{{title}}</span>
          <input type="text" v-model="inputData" @keyup.enter="event()">
              <p v-if="onA == 0">{{problem}}</p>
              <p v-else>{{problem[0]}} * {{problem[1]}}</p>
              <h4>{{result}}</h4>
          <button class="svc_btn" @click="event()">버튼</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    data() {
        return {
            game : ["끝말잇기","곱셈"],
            title : "끝말잇기",
            onA : 0,
            inputData : "",
            result : "",
            problem : "김지호"
        }
    },
    methods: {
        detailV(item,index){
            this.title = item
            this.onA = index
            this.inputData = ""
            this.result = ""
            if(index == 0){
                this.problem = "김지호"
            }else{
                this.problem = [Math.floor(Math.random() * 8)+2,Math.floor(Math.random() * 9)+1];
            }
        },
        event(){
            if(this.inputData == ""){
                this.result = "내용을 입력하세요"
            }else{
            if(this.onA == 0){
                    if(this.problem.slice(this.problem.length-1,this.problem.length) === this.inputData.slice(0,1)){
                        this.result = "정답입니다"
                        this.problem = this.inputData
                        this.inputData = ""
                    }else{
                        this.result = "틀렸습니다"
                        this.inputData = ""
                    }
            }else{
                if(this.problem[0]*this.problem[1] === Number(this.inputData)){
                    this.result = "정답입니다"
                    this.problem[0] = Math.floor(Math.random() * 8)+2;
                    this.problem[1] = Math.floor(Math.random() * 9)+1;
                    this.inputData = ""
                }else{
                    this.result = "틀렸습니다"
                    this.inputData = ""
                }
            }
            }
        }
    },
}
</script>
<style scoped>
  .service_wrap nav {
    display: flex;
    justify-content: space-around;
    height: 5rem;
    align-items: center;
  }
  .service_wrap nav a {
    display: block;
    height: 100%;
    width: 100%;
    line-height: 5rem;
    border-bottom: 3px solid rgb(224, 224, 224)
  }
  .service_wrap nav a.On {
    background-color: rgb(244, 233, 255);
    color: rgb(97, 47, 143);
    border-bottom: 3px solid rgb(97, 47, 143)
  }
  .service_content>div {
    height: 50rem;
    border: 1px solid rgb(224, 224, 224);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 5rem;
  }
  .service_content>div p {
    font-weight: 600;
    font-size: 3rem;
  }
  .service_content>div h4 {
    font-size: 1.6rem;
    font-weight: 200;
  }
  .service_content>div span {
    font-size: 10rem;
    display: inline-block;
    margin: 3rem 0;
    color: rgb(97, 47, 143)
  }
  .svc_btn {
    border: none;
    padding: .5rem 2rem;
    border-radius: 3rem;
    cursor: pointer;
  }
</style>